.default {
    padding: 8px 16px;
    background: #E0E0E0;
    box-shadow: 0px 2px 3px rgba(51, 51, 51, 0.2);
    border-radius: 6px;
    border: none;
    color: #3F3F3F;
}
.default:focus {
    background: #AEAEAE;
}
.outline {
    padding: 8px 16px;
    border: 1px solid #3D5AFE;
    border-radius: 6px;
    color: #3D5AFE;
}
.outline:focus {
    background: rgba(41, 98, 255, 0.1);
}